
import {  useRef } from "react";
// import { useNavigate } from "react-router";

const NewItems = () => {

 

  const newitemRef = useRef();
  const quantityRef = useRef();
  // const navigate = useNavigate();
  
  function addNewItem(event) {
    event.preventDefault();
     const enteredNewItem = newitemRef.current.value;
     const enteredQuantity = quantityRef.current.value;
     

     const NewItem={
         newitem: enteredNewItem,
         quantity: enteredQuantity
        };

       

    fetch('https://shoppinglist-18749-default-rtdb.firebaseio.com/shoppinglist.json',
      {
          method: 'POST',
          body: JSON.stringify(NewItem),
          headers:{
             'Content-Type' : 'application/json'
          }
      }
    ).then(()=>{
      window.location.reload(false);
      // navigate('/profile')
  })
    
  }

    return(
        <div>
           <form className='form' onSubmit={addNewItem}>
                <input className='inputitem' id='newitem' required type='text'  ref={newitemRef} ></input>
            <label htmlFor='quantity'>Quantity</label>
                <input className='inputquantity' id='quantity' name="quantity" min="1"  required type='number' ref={quantityRef} ></input>
                <input className='submit' type='submit' value='Add'></input>
          </form>
        </div>
    )
}

export default NewItems;