import { useRef } from "react";
import { useNavigate } from "react-router";

const Register = () => {
 
    const nameRef= useRef();
    const emailRef= useRef();
    const pswRef= useRef();
    const psw2Ref= useRef();
    const navigate = useNavigate();
    
    function onRegister(event) {
        event.preventDefault();


        const enteredName = nameRef.current.value;
        const enteredEmail = emailRef.current.value;
        const enteredPsw = pswRef.current.value;
        const enteredPsw2 = psw2Ref.current.value;

        const newUser= {
           user: enteredName,
           email: enteredEmail,
           password: enteredPsw,
           password2: enteredPsw2
        }
        fetch('https://shoppinglist-18749-default-rtdb.firebaseio.com/register.json',
          {
              method: 'POST',
              body: JSON.stringify(newUser),
              headers:{
                  'Content-Type' : 'application/json'
              }
          }
        
        ).then(() => {
           navigate('/main');

        })
    }


    return(
        <div className='signin_box'>
         <h2 className='h2_signin'>Welcome to the </h2> <h1 className='h1_signin'> Shopping List</h1><h2 className='h2_signin'> app!</h2>
         <h3>This app will help you shopping blabla balaa</h3>
         <h4>Please provide the following informations:</h4>
         <form className='form register_form' onSubmit={onRegister}>
             <label>name</label>
              <input id='name' type='name' required ref={nameRef}></input>
             <label>email</label>
              <input id='email' type='email' required ref={emailRef} ></input>
             <label>password</label>
              <input id='psw' type='password' required ref={pswRef} ></input>
             <label>confirm password</label>
              <input id='psw2' type='password' required ref={psw2Ref} ></input>
              <input className='signin_submit' type='submit' value='Register'></input>
         </form>
        </div>
    )
}

export default Register;