import { Link } from "react-router-dom";

const Nav = () => {
    return(
        <div className='nav'>
            <ul className='navUl'>
                <li><Link className='navLink' to='/settings'> Settings</Link></li>
                <li><Link className='navLink' to='/purchase'> Purchases</Link></li>
                <li><Link className='navLink' to='/type'>Purchase Type</Link></li>
            </ul>
        </div>
    )
}

export default Nav;