import { useState, useEffect } from "react";
import Item from './Item';


const ItemList = ( props) => {
    
    const [counter, setCounter] = useState(0);
    const [totalPrice, setTotalPrice] = useState(0);
    const [price, setPrice] = useState(12);

    const newprice= () =>{
        setPrice(0);
    }
    
    
    useEffect(()=> {
       total();
       cost();
    }, [props.items])

   const total = () => {
    let sum = 0;
    for (let i = 0; i < props.items.length; i++) {
      sum += parseInt(props.items[i].quantity);
      
    }
    setCounter(sum);
  };

   const cost = () =>{
       let sum = 0;
       for (let i = 0; i < props.items.length; i++ ){
           sum += price;
       }
       setTotalPrice(sum)
   }  ;


    return(
        <div >
          
            {props.items.map((item) =>(
              
                <Item key={item.id} id={item.id} item={item} newitem={item.newitem} quantity={item.quantity}  onRemove= {props.onRemove} price={price} newPrice={newprice}/> 
              
           
            )) } 
           
           <div className='Total'>
            <div className='boxTotal'>
                <h4 className='total wordTotal'>Total:</h4>
                <h4 className='total'>{counter}</h4>
            </div>
                <h4 className='total totalPrice'>${totalPrice}</h4>
          </div>
          

            
        </div>
    )
}

export default ItemList;