import {  useRef } from "react";
import {  useNavigate } from "react-router";

const SignIn = () => {
   
    const emailRef= useRef();
    const pswRef= useRef();
    const navigate=useNavigate();
  
    function onSignin(event){
        event.preventDefault();

        const enteredEmail= emailRef.current.value;
        const enteredPsw= pswRef.current.value;

        const userData= {
          email: enteredEmail,
          password: enteredPsw
        }

        fetch('https://shoppinglist-18749-default-rtdb.firebaseio.com/sigin.json',
         { method: 'POST',
         body: JSON.stringify(userData),
         headers:{
             'Content-Type' : 'application/json'
         }
        }

        ).then(()=>{
           navigate('/main');

        })
    }


    return(
        <div className='signin_box'>
         <h2 className='h2_signin'>Welcome Back to the</h2> <h1 className='h1_signin'>Shopping List </h1><h2 className='h2_signin'>app!</h2>
         <h3>Please provide the following informations:</h3>
         <form className='form signin_form' onSubmit={onSignin}>
            <label>name</label>
              <input id='name' type='name' required ></input>
             <label>email</label>
              <input id='email' type='email' required ref={emailRef}></input>
             <label>password</label>
              <input id='psw' type='password' required ref={pswRef}></input>
              <input className='signin_submit' type='submit' value='SignIn' ></input>
         </form>
        </div>
    )
}

export default SignIn;